___CSS_LOADER_EXPORT___.push([module.id, ".timeline[data-v-005bb070]{list-style:none;margin:0 0 30px;padding:0;position:relative}.timeline[data-v-005bb070]:before{background:#ddd;border-radius:2px;bottom:0;content:\"\";left:31px;margin:0;position:absolute;top:0;width:4px}.timeline>li[data-v-005bb070]{margin-bottom:15px;position:relative}.timeline>.time-label>span[data-v-005bb070]{background-color:#fff;border-radius:4px;display:inline-block;font-weight:600;padding:5px}.timeline>li[data-v-005bb070]:after,.timeline>li[data-v-005bb070]:before{content:\" \";display:table}.timeline>li>.fa[data-v-005bb070],.timeline>li>.glyphicon[data-v-005bb070],.timeline>li>.ion[data-v-005bb070]{background:#d2d6de;border-radius:50%;color:#666;font-size:15px;height:30px;left:18px;line-height:30px;position:absolute;text-align:center;top:0;width:30px}.timeline>li>.timeline-item[data-v-005bb070]{background:#fff;border-radius:3px;box-shadow:0 1px 1px rgba(0,0,0,.1);color:#444;margin-left:60px;margin-right:15px;margin-top:0;padding:0;position:relative}.timeline>li>.timeline-item>.time[data-v-005bb070]{color:#999;float:right;font-size:12px;padding:10px}.timeline>li>.timeline-item>.timeline-header[data-v-005bb070]{border-bottom:1px solid #f4f4f4;color:#555;font-size:16px;font-weight:600;line-height:1.1;margin:0;padding:10px}.timeline>li>.timeline-item>.timeline-body[data-v-005bb070],.timeline>li>.timeline-item>.timeline-footer[data-v-005bb070]{padding:10px}", "",{"version":3,"sources":["webpack://./resources/js/components/trainings/One.vue"],"names":[],"mappings":"AA+GA,2BAII,eAAgB,CAFhB,eAAkB,CAClB,SAAU,CAFV,iBAIJ,CACA,kCAMI,eAAgB,CAGhB,iBAAkB,CALlB,QAAS,CAHT,UAAW,CAMX,SAAU,CACV,QAAS,CANT,iBAAkB,CAClB,KAAM,CAEN,SAKJ,CACA,8BAEI,kBAAmB,CADnB,iBAEJ,CACA,4CAII,qBAAsB,CACtB,iBAAkB,CAFlB,oBAAqB,CAFrB,eAAgB,CAChB,WAIJ,CACA,yEACI,WAAY,CACZ,aACJ,CACA,8GAOI,kBAAmB,CACnB,iBAAkB,CAFlB,UAAW,CAHX,cAAe,CADf,WAAY,CAQZ,SAAU,CANV,gBAAiB,CACjB,iBAAkB,CAIlB,iBAAkB,CAElB,KAAM,CAVN,UAWJ,CACA,6CAKI,eAAgB,CAFhB,iBAAkB,CADlB,mCAAsC,CAItC,UAAW,CACX,gBAAiB,CACjB,iBAAkB,CAJlB,YAAa,CAKb,SAAU,CACV,iBACJ,CACA,mDACI,UAAW,CACX,WAAY,CAEZ,cAAe,CADf,YAEJ,CACA,8DAGI,+BAAgC,CADhC,UAAW,CAGX,cAAe,CAEf,eAAgB,CADhB,eAAgB,CALhB,QAAS,CAGT,YAIJ,CAIA,0HACI,YACJ","sourcesContent":["<template>\n    <ul class=\"timeline\" v-if=\"!isLoading\">\n\n        <template v-for=\"(set, index) in training.training.sets\"\n                  :key=\"set.id\">\n            <li class=\"time-label\">\n                <span class=\"bg-red bg-primary text-white\">\n                    Сет №{{ index + 1 }} <span v-if=\"set.quantity\">Подходов: {{ set.quantity }}</span> <span v-if=\"set.comment\">({{ set.comment }})</span>\n                </span>\n            </li>\n\n            <li v-for=\"(activitie, index) in set.activities\">\n                <i class=\"fa bi-clock bg-blue\"></i>\n\n                <div class=\"timeline-item\">\n    <!--                <span class=\"time\"><i class=\"fa bi bi-clock\"></i> 12:05</span>-->\n\n                    <h3 class=\"timeline-header\">{{ activitie.activitie.name }}</h3>\n\n                    <div class=\"timeline-body\">\n                        <div>Повторений: {{ activitie.quantity }}</div>\n                        <div v-if=\"activitie.comment\">{{ activitie.comment }}</div>\n                        <div v-if=\"activitie.activitie.description\">{{ activitie.activitie.description }}</div>\n                        <div class=\"mt-3\">\n                            <button class=\"btn btn-sm btn-success\" @click=\"showResultForm(activitie)\">выполнено</button>\n                            <button v-if=\"activitie.results && activitie.results.length > 0\" class=\"btn btn-sm btn-warning ms-2\" @click=\"showResultList(activitie)\">записано</button>\n                        </div>\n                    </div>\n\n<!--                    <div class=\"timeline-footer\">-->\n<!--                        <a class=\"btn btn-success btn-sm\">выполнено</a>-->\n<!--                    </div>-->\n                </div>\n            </li>\n        </template>\n\n        <li>\n            <i class=\"fa bi bi-clock bg-gray\"></i>\n        </li>\n\n    </ul>\n    <div v-else>Тренировка загружается</div>\n\n    <my-dialog v-model:show=\"addResultsDialogVisible\">\n        <result-form\n            :training_id=\"id\"\n            :activitie=\"currentActivitie\"\n            @savedSuccess=\"resultSavedSuccess\"></result-form>\n    </my-dialog>\n\n    <my-dialog v-model:show=\"showResultsDialogVisible\">\n        <result-list\n            :activitie=\"currentActivitie\"></result-list>\n    </my-dialog>\n</template>\n\n<script>\nimport axios from \"axios\";\nimport ResultForm from \"./ResultForm\";\nimport ResultList from \"./ResultList\";\nimport {mapActions, mapGetters} from \"vuex\";\n\nexport default {\n    name: \"One\",\n    components: {ResultForm, ResultList},\n    props: {\n        id: {\n            type: Number,\n            required: true\n        }\n    },\n    data() {\n        return {\n            addResultsDialogVisible: false,\n            showResultsDialogVisible: false,\n        }\n    },\n    methods: {\n\n        ...mapActions({\n            fetch: 'training/fetch',\n        }),\n\n        showResultForm(activitie) {\n            this.addResultsDialogVisible = true;\n            this.$store.commit('training/setCurrentActivitie', activitie);\n        },\n        showResultList(activitie) {\n            this.showResultsDialogVisible = true;\n            this.$store.commit('training/setCurrentActivitie', activitie);\n        },\n        resultSavedSuccess() {\n            this.addResultsDialogVisible = false;\n            this.$store.commit('training/setCurrentActivitie', null);\n        }\n    },\n    computed: {\n        ...mapGetters({\n            training: 'training/training',\n            isLoading: 'training/isLoading',\n            currentActivitie: 'training/currentActivitie',\n        }),\n    },\n    mounted() {\n        this.$store.commit('training/setId', this.id);\n        this.$store.dispatch('training/fetch')\n    }\n}\n</script>\n\n<style scoped>\n.timeline {\n    position: relative;\n    margin: 0 0 30px 0;\n    padding: 0;\n    list-style: none;\n}\n.timeline:before {\n    content: '';\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    width: 4px;\n    background: #ddd;\n    left: 31px;\n    margin: 0;\n    border-radius: 2px;\n}\n.timeline>li {\n    position: relative;\n    margin-bottom: 15px;\n}\n.timeline>.time-label>span {\n    font-weight: 600;\n    padding: 5px;\n    display: inline-block;\n    background-color: #fff;\n    border-radius: 4px;\n}\n.timeline>li:before, .timeline>li:after {\n    content: \" \";\n    display: table;\n}\n.timeline>li>.fa, .timeline>li>.glyphicon, .timeline>li>.ion {\n    width: 30px;\n    height: 30px;\n    font-size: 15px;\n    line-height: 30px;\n    position: absolute;\n    color: #666;\n    background: #d2d6de;\n    border-radius: 50%;\n    text-align: center;\n    left: 18px;\n    top: 0;\n}\n.timeline>li>.timeline-item {\n    -webkit-box-shadow: 0 1px 1px rgb(0 0 0 / 10%);\n    box-shadow: 0 1px 1px rgb(0 0 0 / 10%);\n    border-radius: 3px;\n    margin-top: 0;\n    background: #fff;\n    color: #444;\n    margin-left: 60px;\n    margin-right: 15px;\n    padding: 0;\n    position: relative;\n}\n.timeline>li>.timeline-item>.time {\n    color: #999;\n    float: right;\n    padding: 10px;\n    font-size: 12px;\n}\n.timeline>li>.timeline-item>.timeline-header {\n    margin: 0;\n    color: #555;\n    border-bottom: 1px solid #f4f4f4;\n    padding: 10px;\n    font-size: 16px;\n    line-height: 1.1;\n    font-weight: 600;\n}\n.timeline>li>.timeline-item>.timeline-body, .timeline>li>.timeline-item>.timeline-footer {\n    padding: 10px;\n}\n.timeline>li>.timeline-item>.timeline-body, .timeline>li>.timeline-item>.timeline-footer {\n    padding: 10px;\n}\n\n</style>\n"],"sourceRoot":""}]);
